	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Cultured Pearls","date":"05 Jun 2000","slug":"/2000-06-05-cultured-pearls/","tags":"Y2000"},"html":"<p>Christopher has a fever this evening over 38.7 C/101.6 F. We noticed that he was awfully quiet.</p>\n<p>First day at the new office today. The technical part of moving went well. The part the moving company was hired to do did not. Our PCs were set up and ready to go, but our keyboards and mice were in our workplace cartons which were not delivered even by the end of the day.</p>\n<p>I was able to find my missing Notes server by the end of the afternoon. It was up and running, but plugged into the wrong hub.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/06/Astraw3.jpg\" height=\"198\" width=\"134\" align=\"right\" alt=\"astra.jpg: Serviervorschlag - Serving suggestion\" border=\"0\" />We are across the street from the Operettenhaus, where <a href=\"http://www.musical.de/cats/de/html/index.html\">'Cats'</a> is performed. I can see the 'Cats' eyes from my desk, as well as the <a href=\"http://www.hamburger-marktplatz.de/kunden/bavaria/default.htm\">Bavaria-St. Pauli brewery</a> (local cult brand Astra - Was dagegen?), which BTW has nothing to do with St. Pauli Girl beer. </p>\n<p>PapaScott (the website) is 6 months old today. Like <a href=\"http://www.vfth.com\">Al</a> said yesterday: \"When I started this, I had no focus. I still don't. I come here everyday, and do whatever the heck I feel like that day.\" I still don't know what I want to do with this website.</p>\n<p><b>Music</b></p>\n<p><a href=\"http://www.peppermint-park.com/cultured-pearls/\">Cultured Pearls</a> are a soul band from Hannover who have had a couple of radio hits, but will probably never break out of Germany (although the albums <a href=\"http://www.amazon.com/exec/obidos/ASIN/B00000JP11\">\"Liquified Days\"</a> and <a href=\"http://www.amazon.com/exec/obidos/ASIN/B00000B73Z\">\"Space Age Honeymoon\"</a> can be found in the States as imports (apologies for the Amazon links; they aren't listed at BN). Their music is pretty sweet nonetheless.</p>"},"next":{"fields":{"title":"It's Just a Pond","date":"04 Jun 2000","slug":"/2000-06-04-its-just-a-pond/"}},"previous":{"fields":{"title":"Stomach Flu","date":"06 Jun 2000","slug":"/2000-06-06-stomach-flu/"}}},{"node":{"fields":{"title":"It's Just a Pond","date":"04 Jun 2000","slug":"/2000-06-04-its-just-a-pond/","tags":"Y2000"},"html":"<p><a href=\"http://Mama.editthispage.com\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/06/crazyboys.jpg\" height=\"262\" width=\"350\" alt=\"crazyboys.jpg: \" border=\"0\" /></a>\n<p><a href=\"http://Mama.editthispage.com\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/06/20000518014.jpg\" height=\"133\" width=\"100\" align=\"right\" onmouseout=\"this.src='http://static.userland.com/images/mausnews/20000518014.jpg'\" onmouseover=\"this.src='http://static.userland.com/images/mausnews/20000518013.jpg'\" alt=\"crh0518bThumb.jpg: \" border=\"0\" /></a>Hey, cool, a <a href=\"http://macros.userland.com/basic/pictureRef\">pictureRef</a> macro! Does it work yet? Yes, it seems to (although Netscape doesn't like the rollover).</p>\n<p><b>Music</b></p>\n<p><a href=\"http://wmf.editthispage.com/\">Wes</a> mentions \"The Man Who\" from Travis, which has apparently just been released in the States. I got this for my birthday in February from my brother-in-law Roland. Mama's comment: \"It's not wierd like the other CDs he gives you.\"</p>\n<p>If McDonald's can sell Big Macs around the world, why can't record companies manage to release music to both the US and Europe?</p>\n<p>Another example is <a href=\"http://music.barnesandnoble.com/search/product.asp?ean=5033197093024\">\"Reload\"</a> from Tom Jones, his curent collection of duet pop remakes, which (AFAIK) isn't scheduled to be released in the States at all. \"Sexbomb\" was the big single. but my favorite is \"Burning Down the House\": the Tiger shouts, Nina Persson of The Cardigans whispers.</p>"},"next":{"fields":{"title":"Big German Women","date":"03 Jun 2000","slug":"/2000-06-03-big-german-women/"}},"previous":{"fields":{"title":"Cultured Pearls","date":"05 Jun 2000","slug":"/2000-06-05-cultured-pearls/"}}},{"node":{"fields":{"title":"Big German Women","date":"03 Jun 2000","slug":"/2000-06-03-big-german-women/","tags":"Y2000"},"html":"<p><a href=\"http://andrea.editthispage.com/\">Andrea</a>, here's a good example of the 'Big German Women' cliché: <a href=\"http://www.stpauligirl.com/main.htm\">St. Pauli Girl</a>, \"Germany's Fun Loving Beer\" which (AFAIK) is only sold outside of Germany. The girl, in this case, is American.</p>\n<p><img src=\"/images/mausnews/mtp.jpg\" align=\"right\" />Our move at work to Millerntorplatz seems to be proceeding with no problems. I was on call, but nobody called. We had set up a substitute firewall and web server at the new office last week, and they covered the web site during the move. I haven't even been to the new location yet. </p>\n<p>Even though I'm not supposed to print my playlist at <a href=\"http://www.live365.com/cgi-bin/directory.cgi?genre=search&searchdesc=papascott&autostart=papascott\">PapaScott Radio</a>, I hope it's OK to give hints...</p>\n<p><b>Music</b></p>\n<p>Reading how Brent was reminiscing about The Clash reminded me that I didn't have any CDs at all fro a band that was one of my favorites at the end of high school, when I first heard <a href=\"http://music.barnesandnoble.com/search/product.asp?ean=74646388525\">\"London Calling\"</a>. Well, sure enough, buying groceries with Christopher this morning, I was \"Lost in the Supermarket\" (actually, it was Marktkauf) and happened upon the CD. Mama saw it when I got home and remarked that I was reverting to my childhood.</p>\n<p>Also, a day without <a href=\"http://music.barnesandnoble.com/search/product.asp?ean=731453082724\">The Supremes</a> is like a day without sunshine...</p>"},"next":{"fields":{"title":"isn't this just the way we live","date":"02 Jun 2000","slug":"/2000-06-02-isnt-this-just-the-way-we-live/"}},"previous":{"fields":{"title":"It's Just a Pond","date":"04 Jun 2000","slug":"/2000-06-04-its-just-a-pond/"}}}],"pathPrefix":"","first":false,"last":false,"index":1059,"pageCount":1101}}

